import { cn } from '../utils';

export class Input extends HTMLElement {
    connectedCallback() {
        const inputElement = document.createElement('input');

        // Combine base class with any user-provided classes
        inputElement.className = this.className

        // Transfer all attributes from the custom element to the input
        Array.from(this.attributes).forEach(attr => {
            inputElement.setAttribute(attr.name, attr.value);
        });

        // Replace the custom element with the new input element
        this.replaceWith(inputElement);
    }
}
